import { inject } from '@angular/core/testing';
import { Injectable, NgModule } from '@angular/core';

import { HttpService } from 'app/@uisftech/common/service/http.service';
import { TZB_HTTP_URL } from 'app/@uisftech/common/service/http.constant';
import { CONTRACT_HTTP_API } from './contract.http.constant';

@Injectable()
export class ContractService {
    constructor(private httpService: HttpService) {
    }

    queryAgreementInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_URL + CONTRACT_HTTP_API.queryAgreementInfo, params);
    }

    creditAgreementInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_URL + CONTRACT_HTTP_API.creditAgreementInfo, params);
    }
    creditAgreementItemAndAddendumInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_URL + CONTRACT_HTTP_API.creditAgreementItemAndAddendumInfo, params);
    }
    creditAgreementTemplateInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_URL + CONTRACT_HTTP_API.creditAgreementTemplateInfo, params);
    }

    submitAgreementInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_URL + CONTRACT_HTTP_API.submitAgreementInfo, params);
    }

    updateAgreementInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_URL + CONTRACT_HTTP_API.updateAgreementInfo, params);
    }
    creditContractPreviewInfo(params) {
        params = this.httpService.handleParams(params);
        return this.httpService.post(TZB_HTTP_URL + CONTRACT_HTTP_API.creditContractPreviewInfo, params);
    }

}
