import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { TempManageBeanActiveIndex0 } from '../../bean/contract-temp-config.bean';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { FormBuilder, FormControl, Validators, FormGroup } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ContractService } from '../../http/contract.http.service';
import { ContractConfigService } from '../../contract-service';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'basic-infor',
  templateUrl: './basic-infor.component.html',
  styleUrls: ['./basic-infor.component.scss']
})
export class BasicInforComponent implements OnInit {
  @Input() updateData: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  agreementTypeParList: any[]//协议大类;
  agreementTypeIdList: any[]//协议类型;
  activeIndex0Bean: TempManageBeanActiveIndex0 = new TempManageBeanActiveIndex0();
  isApprovalOptions: any = [
    { label: '', value: '' }, { label: '是', value: '1' }, { label: '否', value: '0' }
  ];
  // 日期格式化
  ch: any = CALENDAR_CN;
  // 提示信息
  msgs: Message[] = [];
  //校验
  userform: FormGroup;
  detailFlag: boolean;
  constructor(
    public fb: FormBuilder,
    public commfunc: Commfunc,

    public contractService: ContractService,
    public contractConfigService: ContractConfigService
  ) {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;
    this.agreementTypeIdList = codeValues['agreementTypeIdList'];//协议类型
    this.agreementTypeParList = codeValues['agreementTypeParList'];//协议大类

    this.userform = this.fb.group({
      "agreementTypeId": new FormControl('', [Validators.required]),
      "agreementTypePar": new FormControl('', [Validators.required]),
      "agreementName": new FormControl('', Validators.required),
      "agreementDate": new FormControl('', Validators.required),
      "fromDate": new FormControl('', Validators.required),
      "thruDate": new FormControl('', Validators.required),
      'description': new FormControl(''),
      'signaturePerson': new FormControl(''),
      'signatureCompany': new FormControl(''),
      'signatureBank': new FormControl(''),
      'isApproval': new FormControl(''),
    });
    
    // this.activeIndex0Bean.agreementTypePar = 'WJMB';
    //进入提交页面时获取数据
    this.contractConfigService.basicInfoDatas.subscribe(res => {
      this.activeIndex0Bean = res.basicInfo;
      // this.contractConfigService.basicInfoDatas.unsubscribe();
    })


  }

  ngOnInit() {
    let dom = document.getElementById('contents');
    if (dom.offsetWidth < 1000) {
      dom.style.padding = '0';
    }
    if (this.updateData) {
      this.activeIndex0Bean = this.updateData;
      if (this.updateData.detailFlag) {
        this.detailFlag = true;
      }
    }
  }
  //模板大类
  agreementTypeParChange(item) {
    // this.tempType = item;
  }

  save() {
    if (this.updateData && this.updateData.detailFlag) {
      //详情
      this.outValue.emit({ flag: true, data: this.updateData });
    } else {
      //校验的
      for (const i in this.userform.controls) {
        this.userform.controls[i].markAsDirty();
      }
      //处理是否提交
      for (const i in this.userform.controls) {
        if (!this.userform.controls[i].valid) {
          return;
        }
      }
      debugger
      if (this.activeIndex0Bean.agreementDate) {
        let date = new Date(this.activeIndex0Bean.agreementDate);
        this.activeIndex0Bean.agreementDate = this.commfunc.transDateN(date);
      }
      if (this.activeIndex0Bean.fromDate) {
        let date = new Date(this.activeIndex0Bean.fromDate);
        this.activeIndex0Bean.fromDate = this.commfunc.transDateN(date) + ' ' + date.toTimeString().slice(0, 8);
      }
      if (this.activeIndex0Bean.thruDate) {
        let date = new Date(this.activeIndex0Bean.thruDate);
        this.activeIndex0Bean.thruDate = this.commfunc.transDateN(date) + ' ' + date.toTimeString().slice(0, 8);
      }
      let param = JSON.parse(JSON.stringify(this.activeIndex0Bean))
      if (this.activeIndex0Bean && this.activeIndex0Bean.agreementId) {
        //修改
        this.contractService.submitAgreementInfo(param).subscribe(res => {
          if (res.returnCode.code == 'success') {
            this.outValue.emit({ flag: true, data: this.activeIndex0Bean });
          } else {
            this.msgs = [{ severity: 'error', summary: '', detail: res.returnCode.message }];
          }
        })
      } else {
        //新增
        this.contractService.creditAgreementInfo(param).subscribe(res => {
          if (res.returnCode.code == 'success') {
            this.activeIndex0Bean.agreementId = res.agreementId;

            this.outValue.emit({ flag: true, data: this.activeIndex0Bean });
          } else {
            this.msgs = [{ severity: 'error', summary: '', detail: res.returnCode.message }];
          }
        });
      }
    }
  }

  reset() {
    this.activeIndex0Bean = new TempManageBeanActiveIndex0();
  }
}
