import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ContractService } from '../../http/contract.http.service';
import { TempManageBeanActiveIndex2 } from '../../bean/contract-temp-config.bean';
import { ContractConfigService } from '../../contract-service';
import { Message } from 'primeng/primeng';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'application-infor',
  templateUrl: './application-infor.component.html',
  styleUrls: ['./application-infor.component.scss']
})
export class ApplicationInforComponent implements OnInit {
  detailFlag: boolean;
  @Input() inValue: any;
  @Input() updateData: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  // 日期格式化
  ch: any = CALENDAR_CN;
  productList: any[] = [];//产品列表
  selectProductDisplay: boolean;//产品选择
  legalIdList: any[];
  applyObjectList: any[];
  payModeList: any[];
  isForeverContractList: any[];
  activeIndex2Bean: TempManageBeanActiveIndex2 = new TempManageBeanActiveIndex2();
  // 提示信息
  msgs: Message[] = [];
  //校验
  userform: FormGroup;
  constructor(
    public fb: FormBuilder,
    public commfunc: Commfunc,
    public contractService: ContractService,
    public contractConfigService: ContractConfigService
  ) {
    this.commfunc.codeValue();
    const codeValues = this.commfunc.codeDatas;

    this.legalIdList = codeValues['legalIdList'];//合同模板应用的银行标识
    this.applyObjectList = codeValues['applyObjectList'];//合同模板适用的对象标识
    this.payModeList = codeValues['payModeList'];//合同模板适用的支付类型
    this.isForeverContractList = codeValues['isForeverContractList'];//合同模板适用的合同类型

    //进入提交页面时获取数据
    this.contractConfigService.applicationInfoDatas.subscribe(res => {
      this.activeIndex2Bean = res.applicationInfo;
      this.productList = res.productList;
      if (res.detailFlag) {
        this.detailFlag = true;
      }
      // this.contractConfigService.applicationInfoDatas.unsubscribe();
    })

  }

  ngOnInit() {
    let dom = document.getElementById('content');
    if (dom.offsetWidth < 1000) {
      dom.style.padding = '0';
    }

    this.userform = this.fb.group({
      "legalId": new FormControl('', [Validators.required]),
      "applyObject": new FormControl('', [Validators.required]),
      "payMode": new FormControl('', Validators.required),
      "isForeverContract": new FormControl('', Validators.required)
    });
    if (this.updateData) {
      this.activeIndex2Bean = this.updateData;
      this.productList = this.updateData.productList
      if (this.updateData.detailFlag) {
        this.detailFlag = true;
      }
    }
    setTimeout(() => {
      this.productList = this.productList.length > 0 ? this.productList : [];
    }, 500)
    // this.updateData.
  }
  //选择产品
  choosePro() {
    this.selectProductDisplay = true;
  }
  //选择产品回调
  productCall(event) {
    this.selectProductDisplay = false;
    let list = JSON.parse(JSON.stringify(this.productList));
    list.push({ productNo: event[0].productId, productName: event[0].productName });
    this.productList = list;
  }
  //删除产品
  deletepro(i) {
    let list = JSON.parse(JSON.stringify(this.productList));
    list.splice(i, 1);
    this.productList = list;
  }

  save() {
    if (this.updateData && this.updateData.detailFlag) {
      this.outValue.emit({ flag: true, data: this.activeIndex2Bean, productList: this.productList })
    } else {
      if (this.productList.length > 0) {
        let list = [];
        this.productList.forEach(item => {
          list.push(item.productNo);
        });
        this.activeIndex2Bean.productType = list.join(',');
      } else {
        this.msgs = [{ severity: 'error', summary: '', detail: '请选择产品' }];
        return;
      }
      //校验的
      for (const i in this.userform.controls) {
        this.userform.controls[i].markAsDirty();
      }
      //处理是否提交
      for (const i in this.userform.controls) {
        if (!this.userform.controls[i].valid) {
          return;
        }
      }
      this.activeIndex2Bean.agreementItemSeqId = this.inValue.agreementItemSeqId;
      this.activeIndex2Bean.agreementId = this.inValue.agreementId;
      let param = JSON.parse(JSON.stringify(this.activeIndex2Bean));
      this.contractService.creditAgreementTemplateInfo(param).subscribe(res => {
        if (res.returnCode.code == 'success') {
          this.outValue.emit({ flag: true, data: this.activeIndex2Bean, productList: this.productList })
        } else {
          this.msgs = [{ severity: 'error', summary: '', detail: res.returnCode.message }];
        }
      });
    }
  }

  reset() {
    this.activeIndex2Bean = new TempManageBeanActiveIndex2();
    this.productList = [];
  }
}