import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { TZB_CONTRACT_UPLOAD } from 'app/@uisftech/common/service/http.constant';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { TempManageBeanActiveIndex0, TempManageBeanActiveIndex2 } from '../bean/contract-temp-config.bean';
import { ContractService } from '../http/contract.http.service';
import { Message, MenuItem } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { join } from 'path';
import { BasicInforComponent } from './basic-infor/basic-infor.component';
import { ApplicationInforComponent } from './application-infor/application-infor.component';
import { ContentInforComponent } from './content-infor/content-infor.component';

@Component({
  selector: 'app-temp-manage',
  templateUrl: './temp-manage.component.html',
  styleUrls: ['./temp-manage.component.scss']
})
export class TempManageComponent implements OnInit {
  detailFlag: boolean;
  fileList: any[];
  productList: any[];//产品列表
  activeIndex0Bean: TempManageBeanActiveIndex0 = new TempManageBeanActiveIndex0();
  activeIndex2Bean: TempManageBeanActiveIndex2 = new TempManageBeanActiveIndex2();
  @ViewChild('basicInfo')
  basicInfo: BasicInforComponent;
  @ViewChild('applicationInfo')
  applicationInfo: ApplicationInforComponent;
  @ViewChild('contentInfo')
  contentInfo: ContentInforComponent;
  tempType: string;
  activeIndex: number;
  items: MenuItem[] = [];
  infoData: any;
  // 提示信息
  msgs: Message[] = [];
  updateDatas: any;//修改详情数据
  blockedPanel: boolean;
  constructor(
    public fb: FormBuilder,
    public commfunc: Commfunc,
    public routerInfo: ActivatedRoute,
    public contractService: ContractService,
    public router: Router
  ) {

    this.items = [
      {
        label: '合同模板基本信息配置',
        command: (event: any) => {
          this.activeIndex = 0;
        }
      },
      {
        label: '合同模板内容信息',
        command: (event: any) => {
          this.activeIndex = 1;
        }
      },
      {
        label: '合同模板的应用配置',
        command: (event: any) => {
          this.activeIndex = 2;
        }
      },
    ];
  }

  ngOnInit() {
    this.activeIndex = 0;
    //修改、详情数据回传作回显
    if (this.routerInfo.snapshot.params.data) {
      let data = JSON.parse(this.routerInfo.snapshot.params.data);
      data['legalId'] = data['partyId'];
      data['applyObject'] = data['objectId']
      this.updateDatas = data;
      this.updateDatas.detailFlag ? this.detailFlag = true : this.detailFlag = false;
    }
  }

  //上一步
  prev() {
    this.activeIndex > 0 ? this.activeIndex -= 1 : this.activeIndex;
    switch (this.activeIndex) {
      case 0:
        this.updateDatas = this.activeIndex0Bean;
        break;
      case 1:
        this.updateDatas = this.fileList;
        break;
    }
  }

  //基础信息回调
  basicInfoCall(res) {
    this.activeIndex0Bean = res.data;
    this.infoData = {
      agreementId: this.activeIndex0Bean.agreementId,
      fromDate: this.activeIndex0Bean.fromDate,
      tempType:this.activeIndex0Bean.agreementTypePar
    };
    this.nextStep(res.flag);
  }

  //内容回调
  contentInfoCall(res) {
    this.infoData['agreementItemSeqId'] = res.agreementItemSeqId;
    this.fileList = res.fileList;
    this.nextStep(res.flag);
  }

  //应用回调
  applicationInfoCall(res) {
    this.activeIndex2Bean = res.data;
    this.productList = res.productList;
    this.nextStep(res.flag);
  }

  //跳转下一页
  nextStep(res) {
    if (res) {
      this.activeIndex += 1;
      if (this.activeIndex > 2) {
        let param = {
          basicInfo: this.activeIndex0Bean,
          applicationInfo: this.activeIndex2Bean,
          productList: this.productList,
          fileList: this.fileList,
          detailFlag:this.detailFlag
        }
        this.router.navigate(['/pages/tzb/custom/contract-temp/entirety', { data: JSON.stringify(param) }]);
      }
    }
  }

  //下一步
  next() {
    switch (this.activeIndex) {
      case 0: this.basicInfo.save(); break;
      case 1: this.contentInfo.save(); break;
      case 2: this.applicationInfo.save(); break;
    }
  }

  //取消
  cancel() {
    this.router.navigate(['/pages/tzb/custom/contract-temp']);
  }

  //重置
  rest() {
    switch (this.activeIndex) {
      case 0: this.basicInfo.reset(); break;
      case 2: this.applicationInfo.reset(); break;
    }
  }
}
