import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { TZB_CONTRACT_UPLOAD } from 'app/@uisftech/common/service/http.constant';
import { Message } from 'primeng/primeng';
import { ContractService } from '../../http/contract.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'content-infor',
  templateUrl: './content-infor.component.html',
  styleUrls: ['./content-infor.component.scss']
})
export class ContentInforComponent implements OnInit {
  fileList: { fileName: any; filePath: any; };
  files: any[] = [];
  agreementItemSeqId: string;
  @Input() inValue: any;
  @Input() updateData: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  UPLOAD: any = TZB_CONTRACT_UPLOAD;
  // 提示信息
  msgs: Message[] = [];
  tempType: string;
  detailFlag: boolean;
  constructor(
    public contractService: ContractService,
    public commfunc: Commfunc
  ) { }

  ngOnInit() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let path = `Customer/ContractTempConfig/${this.commfunc.transDate(new Date())}/${commonHeader['userId']}`;
    this.UPLOAD = `${TZB_CONTRACT_UPLOAD}?path=${path}`;
    this.tempType = this.inValue.tempType;
    if (this.updateData) {
      this.agreementItemSeqId = this.updateData.agreementItemSeqId;
      this.files = [{ name: this.updateData.fileName }];
      if (this.updateData) {
        this.updateData.detailFlag ? this.detailFlag = true : this.detailFlag = false;
        this.fileList = {
          fileName: this.updateData.fileName,
          filePath: this.updateData.addendumText
        }
      }
    }
  }

  //上传
  load(event) {
    this.files.push(event.files[0]);
    let fileReponse = event.xhr.response
    if (fileReponse.hasOwnProperty('errorMsg')) {
      this.msgs = [{ severity: 'error', summary: '提示', detail: fileReponse.errorMsg }];
    }
    let param = {
      agreementId: this.inValue.agreementId,
      agreementItemSeqId: this.agreementItemSeqId ? this.agreementItemSeqId : '',
      addendumEffectiveDate: this.inValue.fromDate,
      fileName: JSON.parse(event.xhr.response)[0].fileName,
      filePath: JSON.parse(event.xhr.response)[0].filePath
    }
    this.fileList = {
      fileName: JSON.parse(event.xhr.response)[0].fileName,
      filePath: JSON.parse(event.xhr.response)[0].filePath
    }
    
    this.contractService.creditAgreementItemAndAddendumInfo(param).subscribe(res => {
      if (res.returnCode.code == 'success') {
        this.agreementItemSeqId = res.agreementItemSeqId;
      } else {
        this.msgs = [{ severity: 'error', summary: '', detail: res.returnCode.message }];
      }
    })
  }
  save() {
    if (this.updateData && this.updateData.detailFlag) {
      this.outValue.emit({ flag: true, fileList: this.fileList })
    } else {
      if (this.agreementItemSeqId) {
        this.outValue.emit({ flag: true, agreementItemSeqId: this.agreementItemSeqId, fileList: this.fileList, files: this.files })
      }
    }
  }
}

