import { Component, OnInit, Input } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { ContractConfigService } from '../contract-service';
// import { TZB_CONTRACT_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { ContractService } from '../http/contract.http.service';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'app-preview',
  templateUrl: './preview.component.html',
  styleUrls: ['./preview.component.scss']
})
export class PreviewComponent implements OnInit {
  @Input() inValue: any;
  safeurl: any;
  // previewUrl: string = TZB_CONTRACT_DOWNLOAD
  // 提示信息
  msgs: Message[] = [];
  contractflag: boolean = true;
  count: number = 0;//计数
  constructor(
    public domSanitizer: DomSanitizer,
    public contractService: ContractService,
    public contractConfigService: ContractConfigService
  ) {
    this.contractConfigService.preview.subscribe(res => {
      let data = res.fileList;
      this.count++;
      if (this.count > 1) return;//会出现多次订阅的现象，计数>1次禁止调用
      this.preview(data.fileName, data.filePath)
    })
  }

  ngOnInit() {
    if (this.inValue) {
      this.preview(this.inValue.fileName, this.inValue.addendumText)
    }
  }

  preview(name, path) {
    let param = {
      fileName: name,
      filePath: path
    }
    this.contractService.creditContractPreviewInfo(param).subscribe(res => {
      if (res.returnCode.code == 'success') {
        this.contractflag = false;
        let dom = document.getElementById('box');
        let styled = document.createElement('style');
        styled.innerHTML = 'span{line-height:27px} table{margin:0 auto}'
        let objdom = document.createElement('div');
        objdom.innerHTML = res.flag == 'doc' ? res.htmlInfo : res.htmlInfo.replace('style=\"width:595.0pt;', '"');
        objdom.appendChild(styled)
        dom.appendChild(objdom)
      } else {
        this.contractflag = true;
        this.msgs = [{ severity: 'error', summary: '', detail: res.returnCode.message }];
      }
    })
  }

  print() {
    document.body.innerHTML = document.getElementById('box').innerHTML;
    window.print();
  }
}
