import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ContractConfigService } from '../contract-service';
import { ContractService } from '../http/contract.http.service';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'app-enirety-temp',
  templateUrl: './enirety-temp.component.html',
  styleUrls: ['./enirety-temp.component.scss']
})
export class EniretyTempComponent implements OnInit {
  detailFlag: boolean;
  agreementId: string;
  // 提示信息
  msgs: Message[] = [];
  constructor(
    public router: Router,
    public routerInfo: ActivatedRoute,
    public contractService: ContractService,
    public contractConfigService: ContractConfigService

  ) {
    let data = JSON.parse(this.routerInfo.snapshot.params.data);
    if(data.basicInfo.detailFlag){
      this.detailFlag = true;
    }
    this.agreementId = data.applicationInfo.agreementId;
    setTimeout(() => {
      this.contractConfigService.basicInfoDatas.emit(data);
      this.contractConfigService.applicationInfoDatas.emit(data);
      this.contractConfigService.preview.emit(data);
    })
  }

  ngOnInit() {
    let dom = document.getElementById('uig12');
    document.getElementById('shadow').style.height = `${dom.offsetHeight}px`;
  }
  /**
   * @param
   * 0 保存，1 提交，2 删除
   */
  submit() {
    let param = {
      statusId: '1',
      agreementId: this.agreementId
    }
    this.contractService.submitAgreementInfo(param).subscribe(res => {
      if (res.returnCode.code == 'success') {
        this.router.navigate(['/pages/tzb/custom/contract-temp']);
      } else {
        this.msgs =[{severity:'error',summary:'',detail:res.returnCode.message}];
      }
    })
  }
  cancel() {
    this.router.navigate(['/pages/tzb/custom/contract-temp']);
  }
}
